import React from 'react'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap'

import { Link } from 'gatsby'
import { css } from 'emotion'

export default class Example extends React.Component {
  constructor(props) {
    super(props)

    this.toggle = this.toggle.bind(this)
    this.state = {
      isOpen: false,
    }
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen,
    })
  }
  render() {
    return (
      <div>
        <Navbar color="inherit" dark expand="md">
          <NavbarBrand href="/">
            <img
              src="./assets/CA-Logo.png"
              height="auto"
              width="60px"
              alt="logo"
              className={css`
                margin-bottom: 0;
              `}
            />
          </NavbarBrand>
          <NavbarToggler
            onClick={this.toggle}
            className={css`
              color: white;
            `}
          />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav
              className="ml-auto"
              navbar
              className={css`
                color: white !important;
              `}
            >
              <NavItem>
                <NavLink href="/#projects">Projects</NavLink>
              </NavItem>
              <NavItem>
                <NavLink>
                  <Link
                    to="/resume"
                    className={css`
                      color: inherit;
                      text-decoration: none !important;
                    `}
                  >
                    Resume
                  </Link>
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    )
  }
}
